.custom-img {
  background-image: url('../../../assets/images/web/champagne.webp');
}
  .frame {
    z-index: 2;
    -webkit-transition: all 2s ease;
    -moz-transition: all 2s ease;
    -ms-transition: all 2s ease;
    transition: all 2s ease;
    transform: scale(1.1);
  }
  .zoomin , .img {
    z-index: 1;
    border-radius: 20px;
  }

  .frame:hover {
   transform: scale(1.08);
  }

  @media screen and (max-width: 1260px) {
    .zoomin {
      margin-bottom: 3rem;
    }
    
  }